export default {
  name: 'v-singer-card',
  props: {
    data: {
      type: Object
    }
  },
  methods: {
    jumpSingerDetail(id) {
      this.$router.push({
        path: '/singer/' + id
      });
    }
  },
  computed: {
    avatarImage() {
      return '' || this.data.picUrl;
    }
  }
};



