export default {
  name: 'v-album-card',
  props: {
    data: {
      type: Object
    }
  },
  methods: {
    jumpAlbumDetail(id) {
      this.$router.push({
        path: '/album/' + id
      });
    }
  },
  computed: {
    albumImage() {
      return '' || this.data.picUrl;
    },
    albumTrans() {
      if (this.data.alias && this.data.alias[0]) {
        return '(' + this.data.alias[0] + ')';
      } else {
        return '';
      }
    }
  }
};



