import { playCount } from '../../../common/js/data';
export default {
  name: 'v-play-list-card',
  props: {
    data: {
      type: Object
    }
  },
  methods: {
    jumpPlayListsDetail(id) {
      this.$router.push({
        path: '/playLists/' + id
      });
    }
  },
  computed: {
    coverImage() {
      return '' || this.data.coverImgUrl;
    }
  },
  filters: {
    playCount(count) {
      return playCount(count);
    }
  }
};



