export default {
  name: 'v-user-card',
  props: {
    data: {
      type: Object
    }
  },
  methods: {
    jumpUserDetail(id) {
      this.$router.push({
        path: '/user/' + id
      });
    }
  },
  computed: {
    avatarImage() {
      return '' || this.data.avatarUrl;
    }
  }
};



